export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
    console.log(this.$route);
    console.log(location.host);
    if(location.host == 'share.earthktv.com'){
      this.$router.push({ path: '/share?no=' })
    }else if(location.host == 'pay.earthktv.com'){
      this.$router.push({ path: '/pay?tradeno=&uid=&product_id=' })
    }
  }
}



